import React, {useEffect, useRef, useState} from "react"
import GoogleMapReact,{fitBounds} from 'google-map-react';
import {IStore} from "../stores/InstantStore";
import {Spin} from "antd";
import Colors from "../constants/Colors";
import {findLocation} from "../functions/findLocation";
import BLEService from "../services/BLEService";
import {info} from "../functions/toast";
import {Params} from "../constants/Params";
import {distanceConversion} from "../functions/Conversions";
import {useHistory} from "react-router-dom";
import {string} from "../locales";


let _compass = false
let old_heading = 0;
let _location = {}
let target_locations = []
let bound_target = {}
let maps = null

const coords = {
    accuracy: 15.98,
    altitude: null,
    altitudeAccuracy: null,
    heading: null,
    latitude: 41.0621346,
    longitude: 28.9844027,
    speed: null,
}

const LaserMeter = () => {
    const ble = IStore.ble
    const history = useHistory()
    const param = Params()
    const map = useRef()
    const div = useRef()




    const [location, setLocation] = useState(null);
    const [targets, setTargets] = useState(null);
    const [heading,setHeading] = useState(old_heading)
    const [compass,setCompass] = useState(_compass)
    const [distance_unit, setDistanceUnit] = useState(null);
    const [loading, setLoading] = useState(false);

    useEffect(()=>{

        try {

            setTimeout(()=>{
                _location={
                    latitude: coords.latitude,
                    longitude: coords.longitude,
                    latitudeDelta: 0.0922,
                    longitudeDelta: 0.0421,
                }
                setLocation(_location);


            },1000)

            /*navigator.geolocation.getCurrentPosition(({coords})=>{
                console.warn(coords)

                _location={
                    latitude: coords.latitude,
                    longitude: coords.longitude,
                    latitudeDelta: 0.0922,
                    longitudeDelta: 0.0421,
                }
                setLocation(_location);
            },(e)=>{
                console.warn(e)
            },{ enableHighAccuracy:true });*/
        }catch (e) {
        }


        BLEService.event.on("distance_and_compass",_setData)

        return()=>{
            BLEService.event.removeListener("distance_and_compass",_setData)
        }
    },[])

    function _setData({distance,distance_unit,angle_unit,azimuth,elevation,roll}){


        if(azimuth==0 || azimuth==180 || azimuth==360)
            azimuth+=0.000001
        azimuth = azimuth%360

        setLoading(false)
        if(!_location?.latitude)
            return;

        target_locations=[]



        for(const val of distance){
            let target = findLocation(_location.latitude,_location.longitude,val,azimuth,elevation,angle_unit,distance_unit);
            target_locations.push({
                ...target,
                height:target.y_distance,
                distance:target.x_distance,
                azimuth,
                polyline:maps?new maps.Polyline({
                    path:[{lat: target.latitude, lng: target.longitude},{lat: _location.latitude, lng: _location.longitude}],
                    geodesic: true,
                    strokeColor: Colors.text,
                    strokeOpacity: 0.7,
                    strokeWeight: 4
                }):null
            })
        }
        bound_target = target_locations[distance.indexOf(Math.max(...distance))]
        setTargets([...target_locations])
        setDistanceUnit(distanceConversion(distance,distance_unit,distance_unit).unit)

        try {
            for(const {polyline} of target_locations){

                polyline.setMap(map.current)

            }
        }catch (e) {

        }

        setTimeout(()=>{
            try {
                if(!_compass && maps){
                    map.current.panTo([...target_locations.map(({latitude,longitude})=>({lat:latitude,lng:longitude})),{lat: _location.latitude, lng: _location.longitude}])
                }
            }catch (e) {}
        },1000)
    }

    function controlDevice() {
        try {
            for(const {polyline} of target_locations){
                polyline.setMap(null)
            }
        }catch (e) {

        }
        setTargets([])
        const device = ble.getDevice()

        if(device){
            findTarget()

        }else{
            history.push("select-device")
        }
    }
    async function findTarget() {

        info("At Yaplyor...")
        setLoading(true)
        await ble.sendDataToDevice("distance_and_compass",param.distance_and_compass.getHex)

    }

    const Marker = ({my,title }) => {
        if(my){
            return(
                <div className="center" style={{marginTop:-15,marginLeft:-15, width:30,height:30,backgroundColor:Colors.primary,borderRadius:100}}>
                    <div style={{width:15,height:15,backgroundColor:Colors.white,borderRadius:100}}></div>
                </div>
            )
        }
        return(
            <div className="flex center column" style={{marginTop:-40}}>
                <img src={require("../assets/images/placeholder.png").default} style={{width:40,height:40}} />
                <div style={{textAlign:'center', width:100, fontSize:10,color:Colors.white,fontWeight:'bold'}}>{title}</div>
            </div>
        )
    };

    if (!location )
        return (
            <div className="contain center">
                <Spin/>
            </div>
        );

    let center={lat: location.latitude, lng: location.longitude},zoom=15;

    if(Array.isArray(targets) && targets.length){


        const res = fitBounds( bound_target.azimuth>180?{
            ne: {
                lat: location.latitude,
                lng: location.longitude
            },
            sw: {
                lat: bound_target.latitude,
                lng: bound_target.longitude
            }
        }:{
            nw: {
                lat: location.latitude,
                lng: location.longitude
            },
            se: {
                lat: bound_target.latitude,
                lng: bound_target.longitude
            }
        }, {
            width: div?.current?.offsetWidth??0, // Map width in pixels
            height: div?.current?.offsetHeight??0, // Map height in pixels
        })
        center = res.center
        zoom = (res.zoom??10)-0.5


    }

    function ConvertDDToDMS(D){
        return [0|D, 'D ', 0|(D=(D<0?-D:D)+1e-4)%1*60, "' ", 0|D*60%1*60, '"'].join('');
    }

    return (
        <div style={{ height: '90vh', width: '100%' }}>
            <div ref={div}  style={{display:'flex', height: '75%', width: '100%' }} >
                <GoogleMapReact
                    bootstrapURLKeys={{ key: "AIzaSyAJ_6n7wqqK8sIk0LV6IqO3OuukpMIbQMM" }}
                    center={center}
                    zoom={zoom}
                    yesIWantToUseGoogleMapApiInternals
                    onGoogleApiLoaded={({ map:_map,maps:_maps }) => {
                        map.current = _map;
                        maps = _maps

                    }}

                    options={{
                        styles: [
                            { elementType: "geometry", stylers: [{ color: "#242f3e" }] },
                            { elementType: "labels.text.stroke", stylers: [{ color: "#242f3e" }] },
                            { elementType: "labels.text.fill", stylers: [{ color: "#746855" }] },
                            ...require("../assets/styles/googleMap.json")
                        ]

                    }}
                >
                    <Marker
                        lat={location.latitude}
                        lng={location.longitude}
                        my
                    />
                    {
                        Array.isArray(targets) && targets.length?
                            targets.map((target,index)=>
                                <Marker
                                    key={index}
                                    lat={target.latitude}
                                    lng={target.longitude}
                                    title={"Hedef "+(index+1)}
                                />
                            )
                            :null
                    }
                </GoogleMapReact>
            </div>
            <div className="flex-1 center" style={{height: '25%', width: '100%'}} >
                <div className="flex-1 row center" style={{alignItems:'center',justifyContent:'space-around'}}>
                    {
                        Array.isArray(targets)?targets.map(({latitude,longitude,height,distance},index)=>
                            <div key={index} style={{padding:10,margin:10,borderRadius:10,backgroundColor:Colors.primary+"22"}}>
                                <div style={{textAlign:'center',width:'100%', fontSize:"1.1rem",color:Colors.white,textDecorationLine:'underline',fontWeight:'bold'}}>{"Hedef "+(index+1)}</div>

                                <div className="flex row">
                                   <div className="flex column" style={{padding:10,alignItems:'center',justifyContent:'space-around'}}>
                                       <div style={{fontSize:"1rem",color:Colors.primary}}>{string.yukseklik}</div>
                                       <div style={{fontSize:"1.1rem",color:Colors.text,fontWeight:'bold'}}>{parseFloat(height).toFixed(2)+distance_unit}</div>
                                   </div>
                                   <div className="flex column" style={{padding:10,alignItems:'center',justifyContent:'space-around'}}>
                                       <div style={{fontSize:"1rem",color:Colors.primary}}>{string.mesafe2}</div>
                                       <div style={{fontSize:"1.1rem",color:Colors.text,fontWeight:'bold'}}>{parseFloat(distance).toFixed(2)+distance_unit}</div>
                                   </div>
                               </div>
                                <div className="flex column" style={{padding:10,alignItems:'center',justifyContent:'space-around'}}>
                                    <div style={{fontSize:"1rem",color:Colors.text,fontWeight:'bold'}}>{ConvertDDToDMS(latitude)+" N"}</div>
                                    <div style={{fontSize:"1rem",color:Colors.text,fontWeight:'bold'}}>{ConvertDDToDMS(longitude)+" E"}</div>
                                </div>
                            </div>
                        ):null
                    }
                    <div onClick={controlDevice} className="flex row center" style={{padding:"10px 15px",margin:10,borderRadius:10,backgroundColor:Colors.primary}}>
                        <div style={{color:Colors.white,fontWeight:'bold',fontSize:14}} >{string.atisyap}</div>
                        <img src={require("../assets/images/measure.png").default} style={{width:25,height:25}} />

                    </div>

                </div>
            </div>
        </div>
    )
}



export default LaserMeter